@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap");

body {
  margin: 0;
  max-width: 100vw;
  overflow-x: hidden;
}

* {
  font-family: "Roboto", sans-serif;
  transform-origin: 0 0;
  outline: none;
}

h1,
h2,
h3 {
  font-weight: 400;
  // font-size: 50px;
}

.relative {
  position: relative;
}

.flex {
  display: flex;
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.main-gradient {
  background: linear-gradient(to right, #ff512f, #dd2476);
}

.main-gradient-light {
  background: linear-gradient(to right, #ff7e64, #db6399);
}

.shadow {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.432);
}

.button,
button {
  padding: 10px 30px;
  max-height: 44px;
  border-radius: 22px;
  border: 1px solid #dd2476;
  background: transparent;
  outline: none;
  text-transform: uppercase;
  color: #dd2476;
  cursor: pointer;
  font-size: 14px;
  user-select: none;

  &:hover {
    background: linear-gradient(to right, #ff512f, #dd2476);
    border: 1px solid transparent;
    color: white;
  }
}

a {
  color: initial;
  text-decoration: none;
}
