.my-tree-list {
  h3 {
    font-size: 26px;
    color: gray;
  }

  .no-trees-message {
    color: rgb(145, 145, 145);
  }

  .my-tree-list-body {
    margin: 30px 0;
  }

  .new-tree-label {
    color: gray;
    text-transform: uppercase;
    font-size: 12px;
  }

  th {
    text-align: left;
    vertical-align: center;
    padding-right: 20px;
    padding-bottom: 20px;
    color: gray;
    font-weight: normal;
  }

  tr {
    border-bottom: 1px solid gray;
  }

  .tree-name {
    font-size: 20px;
  }

  button.delete-button {
    padding: 6px 10px;
    &:hover {
      img {
        filter: invert(100%);
      }
    }
  }

  .small-text {
    font-size: 12px;
    opacity: 0.8;
  }
}
