.container {
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  opacity: 1;
}

@keyframes animation-highlight {
  from {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: 100000%;
  }
}

.container path {
  stroke-linecap: round;
  stroke-linejoin: round;
  border-radius: 20px;
  stroke-miterlimit: 100px;
}

.container.highlighted path {
  transition: all 0.6s ease-in;
  animation-name: animation-highlight;
  animation-duration: 20000s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
  animation-timing-function: linear;
  stroke-dasharray: 15px;
}

.type--union {
  stroke: rgb(255, 158, 231);
  stroke-width: 5px;
  z-index: 1;
}

.type--child {
  stroke-width: 2px;
  stroke: rgb(255, 86, 109);
  z-index: 0;
}

.type--undefined {
  stroke: rgb(0, 0, 0);
}
