.page-header {
  padding: 15px 0;

  @media (min-width: 1000px) {
    padding-top: 50px;
  }

  user-select: none;

  div.flex {
    align-items: flex-end;
  }

  h2 {
    margin: 0;
    margin-left: -8px;
    font-size: 29px;
    color: rgb(255, 181, 181);
    span {
      font-weight: 800;
      color: white;
      margin-right: 0px;
      margin-left: 2px;
    }
  }

  img {
    width: 40px;
    height: 40px;
    margin-left: 10px;
    filter: invert(100%);
    transform: translateY(-5px);
  }
}
