.tree-view-page {
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  hr {
    margin: 0;
  }

  .tree-view-page-header {
    padding: 10px 0 10px 20px;
    height: 40px;
    user-select: none;

    h2 {
      margin: 0;
      margin-right: 20px;
    }

    @media (max-width: 700px) {
      h2 {
        display: none;
      }
    }

    hr {
      opacity: 0.1;
    }

    .button {
      padding: 7px 12px;
      min-height: 10px;
      margin-right: 15px;

      &.disabled {
        opacity: 0.5;
        pointer-events: none;
        cursor: not-allowed;

        &:hover {
          background: none;
        }
      }

      &.minimal-button {
        border-color: transparent;
      }
    }
  }

  .tree-display-container {
    transform-origin: 0 0;
    overflow: scroll;
    width: 100vw;
    height: calc(100vh - 40px);
  }
}
